html {
  min-height: 100% !important;
  height: 100%;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
  width: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.goBack {
  height: 50px;
  width: 50px;
  background-image: url('./images/back-arrow.png');
  background-repeat: no-repeat;
  background-size: contain;
  -webkit-margin-before: 0.83em;
  -webkit-margin-after: 0.83em;
}

.descriptor {
  border-top: 1px solid black;
  justify-content: space-around;
  text-align: center;
  display: flex;
}

.descriptor.first-row {
  border-top: none !important;
}

.MuiTable-root {
  border: 1px solid black;
  text-transform: capitalize;
}

.MuiTableCell-root {
  border-bottom: none !important;
}

.key {
  width: 50%;
  line-height: 1.7;
  font-size: 1.01em;
}

#pokedex {
  height: 100%;
  width: 100%;
}

#information {
  border-right: 1px solid black;
}

.onTop {
  display: flex;
  justify-content: space-between;
  text-transform: capitalize;
}

a {
  text-decoration: none;
  color: darkslategrey;
  line-height: 1.4;
}

a:hover {
  color: lightslategrey;
}

.spinner {
  background-image: url("./images/pokeball-gif-24.gif");
  background-color: white;
  justify-content: center;
  background-repeat: no-repeat;
  width: 500px;
  height: 500px;
  margin: 0 auto;
}

.pokemon-content {
  margin: 0 auto;
  width: 80%;
  overflow-wrap: normal;
  padding: 20px;
}

.MuiDrawer-paper > div > ul {
  width: 225px;
}

.drawer-item {
  text-align: center;
}

/**
 * FourOhFour
 */
.four-oh-four-body {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.four-oh-four-body p {
  font-size: 24px;
  margin-bottom: 2px;
}

.four-oh-four-body span {
  font-size: 15px;
}

.pikachu-gif {
  text-align: center;
}

.pikachu-gif > img {
  height: 240px;
  width: 240px;
  content: url('./images/pikachu.gif')
}

/**
 * About page
 */
 .about {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
}

.about-body {
  width: 50%;
  display: flex;
  margin: 0 auto;
  flex-direction: column;
  font-size: 20px;
}

.about-body .linkToPages {
  padding: 0px 0px 10px 0px;
  font-size: 13px;
  margin: 0;
  list-style: none;
}

.about-body .linkToPages a {
  padding-right: 10px;
}

.about-body .linkToPages a #icon-link {
  height: 25px;
  width: 25px;
  color: black;
}

.about-body .about-project {
  padding-bottom: 10px;
}

.about-body .icons a {
  padding-right: 10px;
}

/* 
 * Main page
 */
.pokeList {
  height: 100%;
}

.content {
  display: flex;
  height: 100%;
}

.generations {
  width: 25%;
  border-right: 1px solid black;
  height: 100%;
}

.entry {
  width: 75%;
}

.MuiAppBar-root {
  background-color: #fff !important;
  color: black !important;
  padding: 0 20px;
}

.MuiButton-contained {
  text-transform: none !important;
}

.pokemon-menu {
  margin-left: auto !important;
}

.about-link {
  color: black !important;
  margin-left: 25px !important;
}

.drawer-list {
  padding-top: 0 !important;
  padding-bottom: 0 !important;
}

/**
 * Home page
 */
.view {
  margin-left: auto;
}

.home-info {
  padding: 4rem;
}

p.main-desc {
  font-size: 1.25rem;
}

span.sub-desc {
  display: block;
  padding: 4px 0;
}

/**
 * Pokemon About Page
 */
.onTop.pokemon-content {
  padding-bottom: 0 !important;
}

h2.pokemon-name-image {
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

@media screen and (min-width: 768px) {
  h2.pokemon-name-image img {
    height: 120px;
    width: 120px;
  }
}